@import url("https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css");
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+TC|Red+Hat+Text&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');

a, a:hover, a:focus {
  color: inherit;
  text-decoration: none;
}

body {
  margin: 0;
  font-family: 'Red Hat Text', 'Noto Sans TC', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1f1f1f;
  padding-top: 90px;
}

main {
  min-height: 90vh;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255,255,255,0.8);
  box-shadow: 0 1px 10px rgba(0,0,0,0.1);
  z-index: 99;
}

.h-1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .nav {
  margin: 0 -20px;
}

.header .nav li {
  position: relative;
  padding: 0 20px;
}

.header .nav li a {
  display: flex;
  height: 90px;
  align-items: center;
  justify-content: center;
}

.footer {
  padding: 90px 0;
  background-color: #f3f3f3f3;
  margin-bottom: 30px;
  position: relative;
  box-shadow: 0 1px 10px rgba(0,0,0,0.1);
}

.totop {
  outline: none !important;
  background: rgba(255,255,255,0.5);
  width: 40px;
  height: 40px;
  position: fixed;
  right: 20px;
  bottom: 20px;
  font-size: 0;
  border: 0;
  border-radius: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' xmlns='http://www.w3.org/2000/svg' fill-rule='evenodd' clip-rule='evenodd'%3E%3Cpath d='M11 2.206l-6.235 7.528-.765-.645 7.521-9 7.479 9-.764.646-6.236-7.53v21.884h-1v-21.883z'/%3E%3C/svg%3E");
  background-size: 24px auto;
  background-position: center;
  background-repeat: no-repeat;
  transition: all 0.3s ease;
  visibility: hidden;
  opacity: 0;
}

.wrapper.scroll-down .totop {
  visibility: visible;
  opacity: 1;
}

.vertical-timeline::before {
  background-color: #f9f9f9;
}

.vertical-timeline--two-columns .vertical-timeline-element-content::before {
  border-left-color: #f2f2f2;
}

.vertical-timeline-element-content {
  background-color: #f2f2f2;
}

.vertical-timeline--animate .vertical-timeline-element-icon.bounce-in {
  background: #ddd !important;
}

.submenu {
  position: absolute;
  visibility: hidden;
  transition: all 0.3s ease;
  opacity: 0;
  background-color: #fff;
  transform: translateY(50px);
  padding: 5px 0;
  top: 100%;
  width: 200px;
  max-width: 300px;
  left: 0;
  box-shadow: 0 1px 10px rgba(0,0,0,0.1);
}

li:hover > .submenu {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.submenu .nav {
  display: block;
  margin: 0;
}

.submenu .nav li {
  padding: 0;
}

.submenu .nav li a {
  display: block;
  height: auto;
  padding: 5px 20px;
  transition: all 0.3s ease;
}

.submenu .nav li a:hover {
  background-color: #f9f9f9;
}

.menu-tg {
  display: none;
}

.wrapper {
  overflow: hidden;
}

.h-logo h1 {
  margin: 0;
}

.submenu-tg {
  display: none;
}

.detailWp,
.aboutWp {
  padding: 60px 0;
}

.detailWp .coverImg {
  margin-bottom: 30px;
}

.detailWp .content {
  margin-top: 50px;
}

.aboutWp h2 {
  margin-bottom: 50px;
}

.share {
  padding: 10px 0;
  border-top: 1px solid rgba(0,0,0,0.2);
  margin-top: 50px;
}

.share ul {
  margin: 0 -10px;
}

.share li {
  margin: 10px;
}

.foot-note {
  margin-top: 50px;
  color: #6d6d6d;
}

.foot-note > div {
  margin-bottom: 5px;
}

.foot-note a:hover {
  text-decoration: underline;
}

.foot-note h4 {
  margin-bottom: 15px;
}

.related {
  margin-top: 50px;
}

.related .next {
  text-align: right;
}

.related a {
  position: relative;
  display: inline-block;
  font-style: italic;
}

.related a:hover {
  text-decoration: underline;
}

.related .prev a {
  padding-left: 45px;
}

.related .next a {
  padding-right: 45px;
}

.related a:before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  border-radius: 50%;
  border: 1px solid;
  top: -3px;
  background-repeat: no-repeat;
  background-size: 16px auto;
}

.related .prev a:before {
  left: 0;
  background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='iso-8859-1'%3F%3E%3Csvg version='1.1' id='Capa_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 477.175 477.175' style='enable-background:new 0 0 477.175 477.175;' xml:space='preserve'%3E%3Cg%3E%3Cpath d='M145.188,238.575l215.5-215.5c5.3-5.3,5.3-13.8,0-19.1s-13.8-5.3-19.1,0l-225.1,225.1c-5.3,5.3-5.3,13.8,0,19.1l225.1,225c2.6,2.6,6.1,4,9.5,4s6.9-1.3,9.5-4c5.3-5.3,5.3-13.8,0-19.1L145.188,238.575z'/%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3C/svg%3E");
  background-position: 45% center;
}

.related .next a:before {
  right: 0;
  background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='iso-8859-1'%3F%3E%3Csvg version='1.1' id='Capa_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 477.175 477.175' style='enable-background:new 0 0 477.175 477.175;' xml:space='preserve'%3E%3Cg%3E%3Cpath d='M360.731,229.075l-225.1-225.1c-5.3-5.3-13.8-5.3-19.1,0s-5.3,13.8,0,19.1l215.5,215.5l-215.5,215.5c-5.3,5.3-5.3,13.8,0,19.1c2.6,2.6,6.1,4,9.5,4c3.4,0,6.9-1.3,9.5-4l225.1-225.1C365.931,242.875,365.931,234.275,360.731,229.075z'/%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3C/svg%3E");
  background-position: 55% center;
}

.vertical-timeline-element {
  transition: all 0.3s ease;
}

.vertical-timeline.hovering .vertical-timeline-element:not(.active) {
  opacity: 0.3;
}

.vertical-timeline.hovering .vertical-timeline-element.active {
  transform: scale(1.05);
}

.vertical-timeline-element-title {
  margin-bottom: 10px;
}

.vertical-timeline--two-columns .vertical-timeline-element-content {
  padding: 0;
}

.vertical-timeline--two-columns .vertical-timeline-element-content > div {
  padding: 1em;
}

.vertical-timeline-element-content .vertical-timeline-element-date {
  margin-left: 1em;
}

.downloadWp > .row {
  margin-bottom: 90px;
}

.downloadWp h3 {
  margin-bottom: 30px;
  padding-left: 15px;
  border-left: 3px solid;
}

.downloadWp a {
  display: block;
  position: relative;
  background: #fff;
  transition: all 0.3s ease;
  margin-bottom: 30px;
}

.downloadWp a:hover {
  transform: scale(1.1);
  box-shadow: 5px 5px 10px rgba(0,0,0,0.05);
}

.downloadWp a img {
  width: 100%;
  background: #fff;
  transition: all 0.3s ease;
  border-radius: 6px;
}

.detailWp :not(.downloadWp) > .downloadWp {
  margin-top: 90px;
}

.detailWp .downloadWp .mt-5 {
  display: none;
}

@media only screen and (min-width: 1170px) {
  .vertical-timeline--two-columns .vertical-timeline-element-icon {
    width: 30px;
    height: 30px;
    margin-top: 15px;
    margin-left: -15px;
  }
  .vertical-timeline--two-columns .vertical-timeline-element:nth-child(even):not(.vertical-timeline-element--left) .vertical-timeline-element-content::before, .vertical-timeline--two-columns .vertical-timeline-element.vertical-timeline-element--right .vertical-timeline-element-content::before {
    border-right-color: #f2f2f2;
  }
  .vertical-timeline--two-columns .vertical-timeline-element-content > div {
    padding: 1.5em;
  }
  .vertical-timeline-element-content .vertical-timeline-element-date {
    margin-left: 0;
  }
}

@media only screen and (max-width: 1199px) {
  .menu,
  .menu-overlap {
    position: fixed;
    visibility: hidden;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .wrapper.menu-open .menu,
  .wrapper.menu-open .menu-overlap {
    visibility: visible;
  }
  .menu > .nav {
    background: #fff;
    max-width: 80vw;
    width: 300px;
    transition: all 0.3s ease;
    transform: translateX(100%);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    box-shadow: -1px 0 10px rgba(0,0,0,0.1);
    display: block;
    padding: 15px 0;
    margin: 0;
  }
  .wrapper.menu-open .menu > .nav {
    transform: translateX(0);
  }
  .header .nav li {
    padding: 0;
  }
  .header .nav li a {
    display: block;
    height: auto;
    padding: 10px 20px;
  }
  .menu-tg {
    display: block;
    width: 80px;
    height: 80px;
    border: 0;
    border-radius: 0;
    outline: none !important;
    background: none;
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' %3F%3E%3C!DOCTYPE svg PUBLIC '-//W3C//DTD SVG 1.1//EN' 'http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd'%3E%3Csvg enable-background='new 0 0 512 512' id='Layer_1' version='1.1' viewBox='0 0 512 512' xml:space='preserve' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg%3E%3Cpath d='M501.5,79.7H10.5C4.7,79.7,0,75,0,69.2c0-5.8,4.7-10.5,10.5-10.5h490.9c5.8,0,10.5,4.7,10.5,10.5 C512,75,507.3,79.7,501.5,79.7z' fill='%236A6E7C'/%3E%3Cpath d='M501.5,266.5H10.5C4.7,266.5,0,261.8,0,256c0-5.8,4.7-10.5,10.5-10.5h490.9c5.8,0,10.5,4.7,10.5,10.5 C512,261.8,507.3,266.5,501.5,266.5z' fill='%236A6E7C'/%3E%3Cpath d='M501.5,453.4H10.5c-5.8,0-10.5-4.7-10.5-10.5s4.7-10.5,10.5-10.5h490.9c5.8,0,10.5,4.7,10.5,10.5 S507.3,453.4,501.5,453.4z' fill='%236A6E7C'/%3E%3C/g%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 24px auto;
    padding: 0;
  }
  .h-logo h1 {
    font-size: 2rem;
  }
  .submenu-tg {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 44px;
    height: 44px;
    background: none;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' %3F%3E%3C!DOCTYPE svg PUBLIC '-//W3C//DTD SVG 1.1//EN' 'http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd'%3E%3Csvg enable-background='new 0 0 100 100' id='Layer_1' version='1.1' viewBox='0 0 100 100' xml:space='preserve' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cpolygon fill='%23010101' points='23.1,34.1 51.5,61.7 80,34.1 81.5,35 51.5,64.1 21.5,35 23.1,34.1 '/%3E%3C/svg%3E");
    background-size: 30px auto;
    border: 0;
    border-radius: 0;
    outline: none !important;
    font-size: 0;
    transition: all 0.3s ease;
  }
  .submenu-tg.open {
    transform: rotate(180deg);
  }
  .submenu {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;
    width: 100%;
    max-width: none;
    visibility: visible !important;
    transform: none !important;
    display: none;
  }
  .submenu-tg.open + .submenu {
    display: block;
  }
  .vertical-timeline-element-content::before {
    transform: rotate(180deg);
    border-right: 0;
  }
  .downloadWp > .row {
    margin: 0 -5px 50px;
  }
  .downloadWp > .row > div {
    padding: 0 5px;
  }
  .downloadWp a {
    margin-bottom: 10px;
  }
}
